.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
  width: 100vw;
  text-align: left;
  background-image: url("./images/childer.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: -255px;
}

.header {
  display: -ms-flexbox;
  display: flex;
  height: 150px;
  padding: 25px;
  color: white;
}

.tagline {
font-size: 21px;
font-weight: 400;
width: 75%;
margin: 10px 0;
}

.logo {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 80px;
}

.main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding: 20px;
}

.button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  outline: none;
  background: rgba(0, 0, 0, 0.1);
  padding: 25px;
  border: none;
  margin: 10px 20px;
  font-size: 28px;
  font-weight: 500;
  color: white;
  width: 100%;
  cursor: pointer;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -o-transition: transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

.button:hover {
  color: #1B3F86;
  background-color: rgba(255, 255, 255, 0.6);
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -o-transition: transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

@-webkit-keyframes logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media (min-width: 330px) {
  .logo {
    top: 15px;
    right: 15px;
    width: 100px;
  }
}

@media (min-width: 450px) {
  .container {
    background-position: center;
  }
}

@media (min-width: 1024px) {
  .header {
    -ms-flex-pack: center;
        justify-content: center;
    padding: 20px;
  }

  .tagline {
  font-size: 24px;
  padding: 50px 80px;
  margin: 0;
  }

  .logo {
    width: 150px;
  }

  .main {
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
  }

  .button {
    padding: 50px;
    width: 240px;
  }

  .button:hover {
    color: #1B3F86;
    background-color: rgba(255, 255, 255, 0.6);
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    -o-transition: transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
  }
}



